import * as actions from "../actions/index"
import * as React from "react"
import { Store } from "../types/index"
import { connect, Dispatch } from "react-redux"
import { Link } from "react-router-dom"
import "../css/index.css"
export function mapStateToProps({ demo: { languageName } }: Store) {
    return {
        name: languageName
    };
}

export function mapDispatchToProps(dispatch: Dispatch<actions.ChangeName>) {
    return {
        changeName: () => dispatch(actions.changeName("name"))
    }
}
export interface Props {
    name: string;
    changeName?: () => void;
}
export interface State {
    multiple: boolean
    imageUp: string
    message: string
    filedata: any
}

class Demo extends React.Component<Props, State>{

    constructor(args: Props) {
        super(args)
        this.state = {
            multiple: false,
            imageUp: "",
            message: "",
            filedata: []
        }
    }

    submit() {
        let res = this.upload(this.state.filedata[0]);
        let self = this;
        res.then((result: any) => {

            let res = JSON.parse(result);
            if (res.status == 200) {
                document.getElementById("message").className = "bg show";
                self.setState({
                    message: "上传成功"
                })
                setTimeout(() => {
                    document.getElementById("message").className = "bg ";
                    self.setState({
                        imageUp: "",
                        filedata: []
                    })
                }, 1500)
            }
        })

    }
    upload(file: string) {
        return new Promise((resolve, reject) => {
            let xhr = new XMLHttpRequest();
            if (xhr.upload) {
                // 文件上传成功或是失败
                xhr.onreadystatechange = (e) => {
                    if (xhr.readyState === 4) {
                        if (xhr.status === 200) {
                            //     // 上传成功操作
                            //     this.handleSuccess(file, xhr.responseText)
                            //     // 把该文件从上传队列中删除
                            //     this.handleDeleteFile(file)
                            resolve(xhr.responseText);
                            // } else {
                            //     // 上传出错处理 
                            //     this.handleFailure(file, xhr.responseText)
                            //     reject(xhr.responseText);
                        }
                    }
                }
            }
            // 开始上传
            xhr.open("POST", "/identity/upload", true)
            let form = new FormData()
            form.append("filedata", file)
            xhr.send(form)
        })
    }
    //上传触发事件
    clickupdate() {
        //this.refs.picture.click();
        document.getElementById("picture").click();
    }


    updateFun(e: any) {
        let files = e.target.files
        let self = this
        if (files.length > 0) {
            //判断是否支持多图上传
            let count = this.state.multiple ? files.length : 1
            //创建本地预览地址
            for (let i = 0; i < count; i++) {
                files[i].thumb = URL.createObjectURL(files[i])
            }
            // 转换为真正的数组: 因为files是类数组, 所以并没有slice方法,这里劫持array在原型链上的slice方法,将该方法给files使用.返回一个真数组
            files = Array.prototype.slice.call(files, 0)
            // 转为数组是为了使用数组上的filter过滤函数
            files = files.filter((file: any) => /image/i.test(file.type));
            //遍历出来有图片的话
            if (files[0].size > 2000000) {
                document.getElementById("message").className = "bg show";
                self.setState({
                    message: "请传入不大于2MB的图片"
                })
                setTimeout(() => {
                    document.getElementById("message").className = "bg ";
                    self.setState({
                        imageUp: "",
                        filedata: []
                    })
                }, 1500)
            };
            if (files.length > 0) {
                this.setState({
                    imageUp: files[0].thumb,
                    filedata: files
                })
            }
        }
    }
    render() {
        return (<article>
            <header id="app">上传身份认证信息</header>
            <article className="body">
                <div onClick={this.clickupdate.bind(this)} className="updateBox">
                    {this.state.imageUp == "" ?
                        <div >+ 点击上传</div>
                        : <div ><img width="100%" src={this.state.imageUp} /></div>
                    }

                    <input name="filename" id="picture" onChange={(e) => this.updateFun(e)} type="file" multiple={this.state.multiple} style={{ display: "none" }} />
                </div>
                {this.state.imageUp == "" ?
                    <div style={{ backgroundColor: "#999" }} className="submit">确定上传</div>
                    : <div onClick={this.submit.bind(this)} className="submit">确定上传</div>
                }

            </article>
            <div className="bg" id="message">
                <div>{this.state.message}</div>
            </div>
        </article>)
    }
}





function mergeProps(stateProps: Object, dispatchProps: Object, ownProps: Object) {
    return Object.assign({}, ownProps, stateProps, dispatchProps);
}

export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(Demo);



